import { parseDom } from "./dom-parser.js";
import { toCanvas } from "./toCanvas.js";
/**
 * HTML
 * @param {HTMLElement} [el] html
 * @param options 
 * @param {Function} [callback] 
 * @return Promise<string|HTMLCanvasElement> base64
 */
export function htmlShot(el, options, callback) {
    return toCanvas(parseDom(el), options, callback);
}
